.header {
  display: flex;
}

.header h2 {
  margin: 0 0;
}

.list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 0;
  margin-top: 0;
}

.list li {
  background-color: rgb(235, 245, 253);
  margin: 5px 0;
}

.list li h3 a {
  margin: 0px;
  color: inherit;
  text-decoration: none;
}