.header {
  display: flex;
}

.header h2 {
  margin: 0 0;
}

.header p {
  margin: 0 0;
  margin-left: auto;
}

.list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 0;
  margin-top: 0;
}

.list li {
  background-color: rgb(235, 245, 253);
  margin: 5px 0;
}

.list li h3 {
  margin: 0px;
}

.list li p {
  margin: 0px;
}